"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { jobsAPI } from "@/utils/api";
import Button from "@/components/Button";

export default function ManageJobsPage() {
  const [employerJobs, setEmployerJobs] = useState([]);
  const [loading, setLoading] = useState(true);
  const [totalApplicants, setTotalApplicants] = useState(0);

  useEffect(() => {
    document.title = "Saksham Rojgar - Manage Jobs";
    fetchJobs();
  }, []);

  const fetchJobs = async () => {
    try {
      setLoading(true);
      const data = await jobsAPI.getEmployerJobs();
      setEmployerJobs(data);
      const total = data.reduce((sum, job) => sum + (job.applicants || 0), 0);
      setTotalApplicants(total);
    } catch (error) {
      console.error('Error fetching jobs:', error);
    } finally {
      setLoading(false);
    }
  };

  const handleDelete = async (jobId) => {
    if (confirm("Are you sure you want to delete this job posting?")) {
      try {
        await jobsAPI.deleteJob(jobId);
        fetchJobs();
      } catch (error) {
        alert('Failed to delete job');
      }
    }
  };

  const handleEdit = (jobId) => {
    alert(`Edit functionality coming soon for job ${jobId}`);
  };

  if (loading) {
    return (
      <div className="p-8 flex justify-center items-center min-h-screen">
        <div className="text-center">
          <div className="text-4xl mb-4">‚è≥</div>
          <p className="text-gray-600">Loading jobs...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="p-8">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900 mb-2">Manage Jobs</h1>
            <p className="text-gray-600">View and manage all your job postings</p>
          </div>
          <Link 
            href="/employer/post-job"
            className="px-6 py-3 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors font-medium"
          >
            + Post New Job
          </Link>
        </div>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-8">
        <div className="bg-white rounded-lg shadow-md p-4 border-l-4 border-blue-400">
          <p className="text-sm text-gray-600 mb-1">Total Posted</p>
          <p className="text-2xl font-bold text-gray-900">{employerJobs.length}</p>
        </div>
        <div className="bg-white rounded-lg shadow-md p-4 border-l-4 border-green-400">
          <p className="text-sm text-gray-600 mb-1">Active Jobs</p>
          <p className="text-2xl font-bold text-green-600">{employerJobs.length}</p>
        </div>
        <div className="bg-white rounded-lg shadow-md p-4 border-l-4 border-purple-400">
          <p className="text-sm text-gray-600 mb-1">Total Applications</p>
          <p className="text-2xl font-bold text-purple-600">{totalApplicants}</p>
        </div>
      </div>

      {/* Jobs List */}
      <div className="bg-white rounded-lg shadow-md">
        {employerJobs.length > 0 ? (
          <div className="divide-y">
            {employerJobs.map((job) => (
              <div key={job._id} className="p-6">
                <div className="flex items-start justify-between mb-4">
                  <div className="flex-1">
                    <div className="flex items-center space-x-3 mb-2">
                      <h3 className="text-xl font-semibold text-gray-900">{job.title}</h3>
                      <span className={`px-3 py-1 rounded-full text-xs font-semibold ${
                        job.jobType === "Full-time" ? "bg-blue-100 text-blue-800" :
                        job.jobType === "Part-time" ? "bg-green-100 text-green-800" :
                        "bg-purple-100 text-purple-800"
                      }`}>
                        {job.jobType}
                      </span>
                    </div>
                    
                    <div className="flex flex-wrap gap-4 text-sm text-gray-600 mb-3">
                      <span className="flex items-center">
                        <span className="mr-1">üìç</span>
                        {job.location}
                      </span>
                      <span className="flex items-center">
                        <span className="mr-1">üí∞</span>
                        {job.salary}
                      </span>
                      <span className="flex items-center">
                        <span className="mr-1">üë•</span>
                        {job.applicants || 0} applicants
                      </span>
                      <span className="flex items-center">
                        <span className="mr-1">üìÖ</span>
                        Posted: {new Date(job.createdAt).toLocaleDateString()}
                      </span>
                    </div>

                    <p className="text-gray-600 line-clamp-2">{job.description}</p>
                  </div>
                </div>

                <div className="flex items-center justify-between pt-4 border-t">
                  <div className="flex space-x-3">
                    <Link
                      href={`/jobs/${job._id}`}
                      className="text-sm font-medium text-blue-600 hover:text-blue-700"
                    >
                      View Details
                    </Link>
                    <Link
                      href="/employer/applicants"
                      className="text-sm font-medium text-green-600 hover:text-green-700"
                    >
                      View Applicants ({job.applicants || 0})
                    </Link>
                  </div>
                  
                  <div className="flex space-x-2">
                    <button
                      onClick={() => handleEdit(job._id)}
                      className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200 transition-colors"
                    >
                      Edit
                    </button>
                    <button
                      onClick={() => handleDelete(job._id)}
                      className="px-4 py-2 text-sm font-medium text-red-600 bg-red-50 rounded-md hover:bg-red-100 transition-colors"
                    >
                      Delete
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center py-16">
            <div className="text-6xl mb-4">üì≠</div>
            <h3 className="text-2xl font-semibold text-gray-900 mb-2">No Jobs Posted Yet</h3>
            <p className="text-gray-600 mb-6">Start by posting your first job</p>
            <Link 
              href="/employer/post-job"
              className="inline-block px-6 py-3 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition-colors font-medium"
            >
              Post Your First Job
            </Link>
          </div>
        )}
      </div>
    </div>
  );
}
